import { Component, OnInit } from '@angular/core';
import {FormControl, FormGroup, FormBuilder, Validators} from '@angular/forms';
import {ContactService} from '../contact.service';
import { FormsModule,  } from '@angular/forms';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {
  ContactForm: FormGroup;
  // tslint:disable-next-line: variable-name
  constructor(private fb: FormBuilder, private _contactService: ContactService ) { }
message = '';
  ngOnInit() {

    this.ContactForm = this.fb.group({
      name: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]],
      phone: ['', [Validators.required, Validators.minLength(10), Validators.maxLength(10)]],
      message: ['', Validators.required]

     });
  }

  onSubmit() {
//alert(this.ContactForm.value);
   this._contactService.createContact(this.ContactForm.value).subscribe(data => {
//alert(this.ContactForm.value);
    // alert('Thank you for sending us an Email,We will get back to you soon');
     this.message = 'Thank you for sending us an Email,We will get back to you soon';
     });
  }

}
